import { isObject } from "../util"
import { arrayMethods } from './array';
import Dep from './dep';

// å¦‚æœç»™å¯¹è±¡æ–°å¢ä¸€ä¸ªå±æ€§ä¸ä¼šè§¦å‘è§†å›¾æ›´æ–°  (ç»™å¯¹è±¡æœ¬èº«ä¹Ÿå¢åŠ ä¸€ä¸ªdepï¼Œdepä¸­å­˜watcherï¼Œå¦‚æœå¢åŠ ä¸€ä¸ªå±æ€§åï¼Œæˆ‘å°±æ‰‹åŠ¨çš„è§¦å‘watcherçš„æ›´æ–°)

class Observer {
    constructor(data) {
        this.dep = new Dep()
        // ç»™å½“å‰çš„æ•°ç»„æ·»åŠ ä¸€ä¸ªå±æ€§ï¼Œå½“å‰çš„observerçš„å®ä¾‹
        // data.__ob__ = this
        // è¿™é‡Œç”¨__ob__ä¼šå‡ºç°æ­»å¾ªç¯ï¼Œå› ä¸ºä¼šä¸åœçš„æ‰§è¡ŒdefineReactive
        Object.defineProperty(data, '__ob__', {
            value: this,
            enumerable: false // ä¸å¯æšä¸¾
        })

        // å¯¹å¯¹è±¡ä¸­çš„æ‰€æœ‰å±æ€§è¿›è¡ŒåŠ«æŒ
        if (Array.isArray(data)) {
            // åˆ‡ç‰‡ç¼–ç¨‹ï¼ˆé«˜é˜¶å‡½æ•°ï¼‰ï¼Œæ”¹å†™åŸæ¥çš„æ•°ç»„
            // ç”¨æˆ·è°ƒç”¨æ—¶ï¼šå¦‚æœè°ƒç”¨çš„æ˜¯â€œåŠ«æŒâ€çš„ä¸ƒä¸ªå˜å¼‚æ–¹æ³•ï¼Œå°±ç”¨è‡ªå·±é‡å†™çš„ï¼Œå¦åˆ™è°ƒç”¨åŸæ¥çš„ã€‚
            data.__proto__ = arrayMethods
            // å¦‚æœæ•°ç»„é‡Œé¢æ˜¯å¯¹è±¡çš„è¯ï¼Œéœ€è¦å¯¹å¯¹è±¡è¿›è¡Œç›‘æ§
            this.observeArray(data)
        } else {
            this.walk(data)
        }
    }

    observeArray(data) {
        // éå†data
        data.forEach(item => {
            // å¯¹itemè¿›è¡Œè§‚æµ‹
            // å¯¹æ•°ç»„é‡Œé¢çš„æ•°ç»„å’Œå¯¹è±¡è¿›è¡Œç›‘æ§
            observe(item)
        })
    }
    walk(data) {
        Object.keys(data).forEach(key => {
            // æŠŠå¯¹è±¡ä¸­çš„æ‰€æœ‰çš„å±æ€§å®šä¹‰æˆå“åº”å¼çš„
            defineReactive(data, key, data[key])
        })
    }
}

function dependArray(value) {
    for (let i = 0; i < value.length; i++) {
        let current = value[i] // current æ˜¯æ•°ç»„é‡Œé¢çš„æ•°ç»„
        current.__ob__ && current.__ob__.dep.depend()
        if (Array.isArray(current)) {
            dependArray(current)
        }
    }
}

// vue2ä¸ºä»€ä¹ˆæ€§èƒ½ä½ï¼Ÿ
// å› ä¸ºVue2ä¼šå¯¹å¯¹è±¡è¿›è¡Œéå†ï¼Œå°†æ¯ä¸ªå±æ€§ç”¨definePropertyé‡æ–°å®šä¹‰ï¼ˆå…¨é‡åŠ«æŒï¼‰
function defineReactive(data, key, value) {
    // value æœ‰å¯èƒ½æ˜¯å¯¹è±¡ï¼ˆå¯¹è±¡å¥—å¯¹è±¡ï¼‰ï¼Œé€’å½’åŠ«æŒ
    let childOb = observe(value)
    let dep = new Dep()
    // console.log('childOb', childOb)
    Object.defineProperty(data, key, {
        get() {
            // console.log('key', key)
            // å–å€¼æ—¶å€™æˆ‘å¸Œæœ›å°†watcherå’ŒDepå…³è”èµ·æ¥
            // ä½†æ˜¯è¿™é‡Œæ²¡æœ‰watcher
            if (Dep.target) {
                // è¯´æ˜è¿™ä¸ªgetæ˜¯åœ¨æ¨¡æ¿ä¸­ä½¿ç”¨çš„
                // è®©depè®°ä½watcherï¼Œä¾èµ–æ”¶é›†,å®ƒæ˜¯ä¸€ä¸ªä¾èµ–æ”¶é›†å™¨ 
                dep.depend()
                // console.log(childOb)
                if (childOb) { // childObå¯èƒ½æ˜¯æ•°ç»„ï¼Œä¹Ÿå¯èƒ½æ˜¯å¯¹è±¡ï¼Œå¯¹è±¡ä¹Ÿè¦æ”¶é›†ï¼Œåç»­å†™$setçš„æ—¶å€™éœ€è¦ç”¨åˆ°å®ƒè‡ªå·±çš„æ›´æ–°æ“ä½œ
                    childOb.dep.depend() // è®©æ•°ç»„å’Œå¯¹è±¡ä¹Ÿè®°å½•watcher
                    if (Array.isArray(value)) {
                        dependArray(value)
                    }
                }
            }
            return value
        },
        set(newV) {
            if (newV !== value) {
                observe(newV)
                // å¦‚æœç”¨æˆ·èµ‹å€¼çš„æ˜¯ä¸€ä¸ªæ–°å¯¹è±¡ï¼Œéœ€è¦å°†è¿™ä¸ªå¯¹è±¡è¿›è¡ŒåŠ«æŒ
                value = newV
                dep.notify() // é€šçŸ¥å½“å‰çš„å±æ€§å­˜æ”¾çš„watcheræ‰§è¡Œ
            }

        }
    })
}

export function observe(data) {
    if (!isObject(data)) {
        return
    }
    if (data.__ob__) {
        return data.__ob__
    }

    return new Observer(data)
}