let oldArrayPrototype = Array.prototype
export let arrayMethods = Object.create(oldArrayPrototype)

// arrayMethods.__proto__ = Array.prototype ç»§æ‰¿

let methods = [
    'shift',
    'unshift',
    'pop',
    'push',
    'splice',
    'reverse',
    'sort'
]

// æ”¹å†™çš„ç”¨è‡ªå·±çš„ï¼Œæ²¡æœ‰æ”¹å†™çš„ç”¨åŸæ¥çš„ã€‚
methods.forEach(method => {
    arrayMethods[method] = function (...args) {
        // è¿™é‡Œçš„argsæ˜¯ä¼ å…¥çš„å‚æ•°åˆ—è¡¨
        // æ¯”å¦‚arr.push(1,2,3),åˆ™ ...args = [1, 2, 3]
        oldArrayPrototype[method].call(this, ...args)
        let inserted;
        let ob = this.__ob__;
        switch (method) {
            case 'push':
            case 'unshift':
                inserted = args;
                break;
            case 'splice':
                inserted = args.slice(2);
            default:
                break;
        }
        if (inserted) {
            // å¦‚æœæœ‰æ–°å¢çš„å€¼ï¼Œå°±ç»§ç»­åŠ«æŒï¼Œè¿™é‡Œè¦è§‚æµ‹çš„æ˜¯æ•°ç»„çš„æ¯ä¸€é¡¹
            ob.observeArray(inserted)
        }
        // console.log('obbbbb', ob)
        ob.dep.notify()
    }
})
