const ncname = `[a-zA-Z_][\\-\\.0-9_a-zA-Z]*`; // æ ‡ç­¾å 
const qnameCapture = `((?:${ncname}\\:)?${ncname})`; //  ç”¨æ¥è·å–çš„æ ‡ç­¾åçš„ matchåçš„ç´¢å¼•ä¸º1çš„
const startTagOpen = new RegExp(`^<${qnameCapture}`); // åŒ¹é…å¼€å§‹æ ‡ç­¾çš„ 
const endTag = new RegExp(`^<\\/${qnameCapture}[^>]*>`); // åŒ¹é…é—­åˆæ ‡ç­¾çš„
//           aa  =   "  xxx "  | '  xxxx '  | xxx
const attribute = /^\s*([^\s"'<>\/=]+)(?:\s*(=)\s*(?:"([^"]*)"+|'([^']*)'+|([^\s"'=<>`]+)))?/; // a=b  a="b"  a='b'
const startTagClose = /^\s*(\/?)>/; //     />   <div/>
// const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g; // {{aaaaa}}


export function parserHTML(html) {

    // ast (è¯­æ³•å±‚é¢çš„æè¿° js css html) vdom ï¼ˆdomèŠ‚ç‚¹ï¼‰

    // htmlå­—ç¬¦ä¸²è§£ææˆ å¯¹åº”çš„è„šæœ¬æ¥è§¦å‘ tokens  <div id="app"> {{name}}</div>
    // å°†è§£æåçš„ç»“æœ ç»„è£…æˆä¸€ä¸ªæ ‘ç»“æ„  æ ˆ

    function createAstElement(tagName, attrs) {
        return {
            tag: tagName,
            type: 1,
            children: [],
            parent: null,
            attrs
        }
    }
    let root = null;
    let stack = [];
    function start(tagName, attributes) {
        let parent = stack[stack.length - 1];
        let element = createAstElement(tagName, attributes);
        if (!root) {
            root = element;
        }
        if (parent) {
            element.parent = parent;// å½“æ”¾å…¥æ ˆä¸­æ—¶ ç»§ç»­çˆ¶äº²æ˜¯è°
            parent.children.push(element)
        }
        stack.push(element);
    }
    function end(tagName) {
        let last = stack.pop();
        if (last.tag !== tagName) {
            throw new Error('æ ‡ç­¾æœ‰è¯¯');
        }
    }
    function chars(text) {
        text = text.replace(/\s/g, "");
        let parent = stack[stack.length - 1];
        if (text) {
            parent.children.push({
                type: 3,
                text
            })
        }
    }
    function advance(len) {
        html = html.substring(len);
    }

    function parseStartTag() {
        const start = html.match(startTagOpen);
        if (start) {
            const match = {
                tagName: start[1],
                attrs: []
            }
            advance(start[0].length);
            let end;
            // å¦‚æœæ²¡æœ‰é‡åˆ°æ ‡ç­¾ç»“å°¾å°±ä¸åœçš„è§£æ
            let attr;

            while (!(end = html.match(startTagClose)) && (attr = html.match(attribute))) {
                match.attrs.push({ name: attr[1], value: attr[3] || attr[4] || attr[5] })
                advance(attr[0].length)
            }
            if (end) {
                advance(end[0].length);
            }
            return match;
        }
        return false; // ä¸æ˜¯å¼€å§‹æ ‡ç­¾
    }
    while (html) { // çœ‹è¦è§£æçš„å†…å®¹æ˜¯å¦å­˜åœ¨ï¼Œå¦‚æœå­˜åœ¨å°±ä¸åœçš„è§£æ
        let textEnd = html.indexOf('<'); // å½“å‰è§£æçš„å¼€å¤´  
        if (textEnd == 0) {
            const startTagMatch = parseStartTag(html); // è§£æå¼€å§‹æ ‡ç­¾
            if (startTagMatch) {
                start(startTagMatch.tagName, startTagMatch.attrs)
                continue;
            }
            const endTagMatch = html.match(endTag);
            if (endTagMatch) {
                end(endTagMatch[1]);
                advance(endTagMatch[0].length);
                continue;
            }
        }
        let text; // //  </div>
        if (textEnd > 0) {
            text = html.substring(0, textEnd)
        }
        if (text) {
            chars(text);
            advance(text.length);
        }
    }
    return root;
}


