import { initState } from "./state"
import { compileToFunction } from './compiler/index';
import { mountComponent, callHook } from './lifecycle';
import { mergeOptions } from "./util";

// è¡¨ç¤ºåœ¨vueçš„åŸºç¡€ä¸Šåšä¸€æ¬¡æ··åˆæ“ä½œ
export function initMixin(Vue) {
    Vue.prototype._init = function (options) {
        // è¿™é‡Œçš„thiså°±æ˜¯å½“å‰çš„Vueå®ä¾‹
        const vm = this
        // ç»™$optionsèµ‹å€¼
        // vm.$options = options
        // constructorå§‹ç»ˆæŒ‡å‘åˆ›å»ºå½“å‰å¯¹è±¡çš„æ„é€ å‡½æ•°
        vm.$options = mergeOptions(vm.constructor.options, options)
        console.log('vm.$options', vm.$options)
        // æ²¡æœ‰åˆå§‹åŒ–çš„æ—¶å€™ï¼Œæ‰§è¡ŒbeforeCreate
        callHook(vm, 'beforeCreate')
        initState(vm)
        // åˆå§‹åŒ–ç»“æŸä¹‹åï¼Œæ‰§è¡Œcreated
        callHook(vm, 'created')

        if (vm.$options.el) {
            // å°†æ•°æ®æŒ‚è½½åˆ°è¿™ä¸ªæ¨¡æ¿ä¸Š
            vm.$mount(vm.$options.el)
        }
    }

    Vue.prototype.$mount = function (el) {
        const vm = this
        const options = vm.$options
        el = document.querySelector(el)
        vm.$el = el
        // 1. æŠŠæ¨¡æ¿å˜æˆæ¸²æŸ“å‡½æ•° => è™šæ‹ŸDom => vnode => diff æ›´æ–°è™šæ‹ŸDomï¼Œæœ€åäº§ç”ŸçœŸå®èŠ‚ç‚¹ï¼Œä¸€æ¬¡æ›´æ–°
        if (!options.render) {
            // è¿™ä¸ªrenderæ˜¯ç”¨æˆ·è‡ªå·±å†™çš„render
            let template = options.template
            if (!template && el) {
                // ç”¨æˆ·æ²¡æœ‰è‡ªå·±å†™templateï¼Œå°±å–elçš„å†…å®¹ä½œä¸ºæ¨¡æ¿
                template = el.outerHTML
            }
            let render = compileToFunction(template)
            options.render = render
        }
        mountComponent(vm, el)
    }
}