
export function patch(oldVnode, vnode) {

    if (!oldVnode) {
        return createElm(vnode); // å¦‚æœæ²¡æœ‰elå…ƒç´ ï¼Œé‚£å°±ç›´æ¥æ ¹æ®è™šæ‹ŸèŠ‚ç‚¹è¿”å›çœŸå®èŠ‚ç‚¹
    }



    if (oldVnode.nodeType == 1) {
        // ç”¨vnode  æ¥ç”ŸæˆçœŸå®dom æ›¿æ¢åŸæœ¬çš„domå…ƒç´ 
        const parentElm = oldVnode.parentNode; // æ‰¾åˆ°ä»–çš„çˆ¶äº²
        let elm = createElm(vnode); //æ ¹æ®è™šæ‹ŸèŠ‚ç‚¹ åˆ›å»ºå…ƒç´ 

        // åœ¨ç¬¬ä¸€æ¬¡æ¸²æŸ“å æ˜¯åˆ é™¤æ‰èŠ‚ç‚¹ï¼Œä¸‹æ¬¡åœ¨ä½¿ç”¨æ— æ³•è·å–
        parentElm.insertBefore(elm, oldVnode.nextSibling);

        parentElm.removeChild(oldVnode)


        return elm
    }
}
// åˆ›å»ºçœŸå®èŠ‚ç‚¹çš„

function createComponent(vnode) {
    let i = vnode.data; //  vnode.data.hook.init
    if ((i = i.hook) && (i = i.init)) {
        i(vnode); // è°ƒç”¨initæ–¹æ³•ï¼Œä¼ å…¥vnodeå€¼ 
    }
    if (vnode.componentInstance) { // æœ‰å±æ€§è¯´æ˜å­ç»„ä»¶newå®Œæ¯•äº†ï¼Œå¹¶ä¸”ç»„ä»¶å¯¹åº”çš„çœŸå®DOMæŒ‚è½½åˆ°äº†componentInstance.$el
        return true;
    }

}

function createElm(vnode) {
    let { tag, data, children, text, vm } = vnode
    if (typeof tag === 'string') { // å…ƒç´ 
        // debugger;
        if (createComponent(vnode)) {
            // è¿”å›ç»„ä»¶å¯¹åº”çš„çœŸå®èŠ‚ç‚¹
            return vnode.componentInstance.$el;
        }

        vnode.el = document.createElement(tag); // è™šæ‹ŸèŠ‚ç‚¹ä¼šæœ‰ä¸€ä¸ªelå±æ€§ å¯¹åº”çœŸå®èŠ‚ç‚¹
        children.forEach(child => {
            vnode.el.appendChild(createElm(child))
        });


    } else {
        vnode.el = document.createTextNode(text);
    }
    return vnode.el
}