let id = 0
class Dep {
    // æ¯ä¸€ä¸ªå±æ€§éƒ½åˆ†é…ä¸€ä¸ªDepï¼Œæ¯ä¸€ä¸ªDepå¯ä»¥å­˜æ”¾watcherï¼Œwatchä¸­è¦å­˜æ”¾Dep
    constructor() {
        this.id = id++
        this.subs = [] // ç”¨æ¥å­˜æ”¾watcher
    }
    depend() {
        // Dep.target depé‡Œé¢è¦å­˜æ”¾è¿™ä¸ªwatcher watcherä¸€æ ·è¦å­˜æ”¾dep
        if (Dep.target) {
            // æŠŠdepç»™watcherï¼Œè®©watcherå­˜æ”¾dep
            Dep.target.addDep(this)
        }
    }
    addSub(watcher) {
        this.subs.push(watcher)
    }
    notify() {
        this.subs.forEach(watcher => {
            watcher.update()
        })
    }
}
Dep.target = null

// ç”¨ä¸€ä¸ªæ ˆè§£å†³computerçš„watcheré—®é¢˜
let stack = []
export function pushTarget(watcher) {
    Dep.target = watcher
    stack.push(watcher)
}

export function popTarget() {
    stack.pop()
    Dep.target = stack[stack.length - 1]
}

export default Dep